@import url(https://use.typekit.net/bza5quc.css);
@import url(https://use.typekit.net/bza5quc.css);
@import url(https://use.typekit.net/bza5quc.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  /*border: solid;*/
  text-decoration: none;
  color: white;
}

a:hover {
  color: rgb(26, 26, 26);
  -webkit-transition-duration: 0.45s;
          transition-duration: 0.45s;
}
#HomeLink {
  color: white;
  text-decoration: none;
  -webkit-transition: 0.45s;
  transition: 0.45s;
}
#HomeLink:hover {
  color: rgb(26, 26, 26);
  -webkit-transition: 0.45s;
  transition: 0.45s;
}
.App {
  height: 100vh;
  /*background-image: url("./August 23, 2020---14 mm---0.6 sec at f - 9.0---.JPG");*/
  text-align: center;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

#root {
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#shadow {
  position: absolute;
  top: 0;
  z-index: 0;
  opacity: 0.02;
  background-color: black;
  width: 100vw;
  height: 100vh;
}

#galleryLink {
  padding: 15px;
}

#centeredHero {
  position: relative;
  /* or absolute */
  z-index: 98;
  top: 30vh;
  left: 0;
  bottom: auto;
  right: auto;
  font-family: futura-pt;
  font-style: bold;
  font-weight: 600;
  font-size: calc(60px + 2vmin);
}
#buttonStyle {
  background-color: rgba(255, 255, 255, 0);
  border: solid;
  border-color: white;
  font-family: futura-pt;
  color: white;
  font-size: calc(8px + 2vmin);
  padding-left: 22.5px;
  padding-right: 22.5px;
  padding-top: 15px;
  padding-bottom: 15px;
  -webkit-transition-duration: 0.45s;
          transition-duration: 0.45s;
}
#buttonStyle:hover {
  color: rgb(26, 26, 26);
  border-color: rgb(26, 26, 26);
  -webkit-transition-duration: 0.45s;
          transition-duration: 0.45s;
}
#fontColoring {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
}
#header-no-border {
  position: relative;
  z-index: 99;
  padding-top: 1.75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-family: futura-pt, sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: calc(10px + 2vmin);
  color: white;
}

.sub-heading {
  align-items: center;
  justify-content: top;
  font-family: futura-pt, sans-serif;
  font-weight: 600;
  font-style: italic;
  font-size: calc(6px + 2vmin);
  color: rgba(255, 255, 255, 0.65);
}

#footer {
  position: absolute;
  width: 100vw;
  padding-bottom: 3.75%;
  bottom: 0;
  flex-direction: row;
  align-items: center;
  justify-content: bottom;
  font-family: futura-pt, sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: calc(10px + 2vmin);
  color: white;
}

#homeLink,
#galleryLink {
  text-decoration: none;
  color: #575757;
  font-family: futura-pt, sans-serif;
}

#homeLink {
  font-weight: 600;
}
.fullBody {
  display: grid;
  place-items: center;
}
.Nav {
  grid-area: nav;
  display: grid;
  grid-template-columns: repeat(4, auto);
  align-items: center;
  justify-items: center;
}
.rectangle {
  background-color: white;
  margin: 10px;
}
.masonry {
  width: 85vw;
  /* Masonry container */
  -webkit-column-count: 3;
     -moz-column-count: 3;
          column-count: 3;
  grid-column-gap: 1em;
  -webkit-column-gap: 1em;
     -moz-column-gap: 1em;
          column-gap: 1em;
}
img {
  -webkit-transition: 0.45s;
  transition: 0.45s;
}
img:hover {
  opacity: 55%;
  -webkit-transition: 0.45s;
  transition: 0.45s;
}
.item {
  /* Masonry bricks or child elements */
  background-color: #eee;
  display: inline-block;
  margin: 0 0 1em;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .masonry {
    -webkit-column-count: 2;
       -moz-column-count: 2;
            column-count: 2;
  }
  .rectangle {
    max-width: 60vw;
    max-height: 25vw;
  }
}

@media only screen and (min-width: 1600px) {
  .rectangle {
    margin: 35px;
    max-width: 40vw;
    max-height: 25vw;
  }
}

.lightbox-window {
    position: fixed;
    top: 50%;
    left: 50%;
    height: 100%;
    width: 100%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    z-index: 11;
    background: black;
    display: flex;
}

.lightbox-header-shadow {
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    height: 72px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.288)), to(rgba(0, 0, 0, 0)));
    background-image: linear-gradient(rgba(0, 0, 0, 0.288), rgba(0, 0, 0, 0))
}

.lightbox-image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin: auto;
}

#lightbox-image {
    max-width: 100%;
    max-height: 100%;
}

.back-button {
    height: 20px;
    position: absolute;
    left: 25px;
    top: 30px;
    cursor: pointer;
}

.arrow-container {
    height: 80%;
    width: 30%;
    cursor: pointer;
    display: flex;
    align-items: center;
    position:absolute;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
}

/* override for the right arrow only */
.arrow-container.right-arrow {
    justify-content: flex-end;
}

.left-arrow {
    left: 30px;
}

.right-arrow {
    right: 30px;
}

.arrow-container img {
    height: 72px;
}



.lightbox-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.5);
}

.grid-img {
    cursor: pointer;
}
.parent {
  display: flex;
  width: 95vw;
  height: 95vh;
  flex-direction: row;
  overflow-x: scroll;
  justify-content: top;
}
#pano {
  width: 50vw;
}
.storyText {
  margin-top: 5%;
  color: rgb(87, 87, 87);
  font-family: futura-pt, sans-serif;
  font-size: calc(2vmin);
  margin-left: 20%;
  margin-right: 20%;
}
#buttonRow {
  margin-top: 50px;
}
.active {
  color: rgba(0, 0, 0, 0.5);
}
.FilteringButtons {
  color: black;
  background-color: rgba(0, 0, 0, 0);
  margin: 5px;
  font-family: futura-pt, sans-serif;
  font-weight: 600;
  font-size: calc(20px + 2vmin);
  -webkit-transition: 0.45s;
  transition: 0.45s;
}
.FilteringButtons:hover {
  color: rgba(0, 0, 0, 0.5);
}
.box {
  padding: 0.5rem;
  text-align: center;
  font-family: futura-pt, sans-serif;
  font-size: 16pt;
  margin: 2.5vw;
  width: 40vw;
  height: 25vw;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.5);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  text-align: center;
}
.bottomText {
  color: rgba(0, 0, 0, 0.95);
  font-weight: 600;
  padding: 0.5rem;
  margin: 2.5vw;
}
.subTitle {
  max-width: 50vw;
  font-family: futura-pt, sans-serif;
  font-weight: 400;
  padding: 0.5rem;
  margin: 2.5vw;
  margin-top: -3.5vw;
  color: rgba(0, 0, 0, 0.5);
  text-align: left;
}
.box:hover {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  opacity: 0.65;
}
@media only screen and (max-width: 600px) {
  #pano {
    width: 80vw;
  }
  #panoText {
    max-width: 83vw;
  }
  .parent {
    flex-direction: column;
    place-items: center;
  }
  .box {
    width: 65vw;
    height: 40vw;
  }
  .subTitle {
    max-width: 65vw;
  }
  .storyText {
    margin-top: 5%;
    color: rgb(87, 87, 87);
    font-family: futura-pt, sans-serif;
    font-size: calc(3vmin);
    width: 90vw;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 5%;
  }
}

